
















import { Vue, Component, Prop } from 'vue-property-decorator';

@Component
export default class MessageBox extends Vue {
  name: string = 'message-box';
  @Prop() title!: string;
  @Prop({ type: Boolean, default: false }) hideDefault!: Boolean;
  show = false;

  created() {
    this.show = !this.hideDefault;
  }
}
